  align-items: center;
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
}

.header__box:before {
  content: "";
  width: 480px;
  height: 1px;
  background: linear-gradient(90deg, #3a5b66 0%, #f93 15%, #f93 83%, #3a5b66 100%);
  position: absolute;
  bottom: 0;
  left: 50%;
